
export function LoginClientProvider({ children }) {
  const [error, setErrors] = useState("");
  const [isLoading, setisLoading] = useState(false);
  const [saveData, setSaveData] = useState(null);
  const [token, setToken] = useState(null);
  const [user, setUser] = useState({ email: "", password: "" });

  useEffect(() => {
    const storedUserData = localStorage.getItem("userData");
    const storedToken = localStorage.getItem("token");

    if (storedUserData) setSaveData(JSON.parse(storedUserData));
    if (storedToken) setToken(storedToken);
  }, []);

  function getUserData(eventInfo) {
    const updated = { ...user, [eventInfo.target.name]: eventInfo.target.value };
    setUser(updated);
  }

  async function sendData() {
    try {
      const { data } = await axios.post(
        `https://projectapi-ekhpcndsdgbahqhm.canadacentral-01.azurewebsites.net/api/account/login`,
        user,
        { validateStatus: () => true }
      );

      if (data.errors == null) {
        setisLoading(false);
        localStorage.setItem("token", data.token);
        localStorage.setItem("userData", JSON.stringify(data));

        setToken(data.token);
        setSaveData(data);

        window.location.href = "/";
      } else {
        setisLoading(false);
        setErrors(data.errors);
      }
    } catch (error) {
      setisLoading(false);
      setErrors("ŸÅÿ¥ŸÑ ÿßŸÑÿßÿ™ÿµÿßŸÑ ÿ®ÿßŸÑÿÆÿßÿØŸÖ.");
    }
  }

  function submitForm(e) {
    e.preventDefault();
    setisLoading(true);
    sendData();
  }

  async function updateProfilePicture(file) {
    const token = localStorage.getItem("token");
    if (!token) {
      throw new Error("ŸÑŸÖ Ÿäÿ™ŸÖ ÿßŸÑÿπÿ´Ÿàÿ± ÿπŸÑŸâ ÿßŸÑÿ™ŸàŸÉŸÜÿå Ÿäÿ±ÿ¨Ÿâ ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ ŸÖÿ¨ÿØÿØŸãÿß.");
    }

    try {
      const formData = new FormData();
      formData.append("newProfilePicture", file);

      const response = await fetch(
        "https://projectapi-ekhpcndsdgbahqhm.canadacentral-01.azurewebsites.net/api/Account/update-profile-picture",
        {
          method: "PUT",
          headers: {
            Authorization: `Bearer ${token}`,
          },
          body: formData,
        }
      );

      if (!response.ok) {
        const result = await response.json();
        throw new Error(result.message || "ŸÅÿ¥ŸÑ ÿ±ŸÅÿπ ÿßŸÑÿµŸàÿ±ÿ©");
      }

      const newUrl = await response.text();

      const oldDataRaw = localStorage.getItem("userData");
      if (!oldDataRaw) throw new Error("ÿ®ŸäÿßŸÜÿßÿ™ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ÿ∫Ÿäÿ± ŸÖŸàÿ¨ŸàÿØÿ©");

      const oldData = JSON.parse(oldDataRaw);
      const updatedData = { ...oldData, profilePictureUrl: newUrl };

      localStorage.setItem("userData", JSON.stringify(updatedData));
      setSaveData(updatedData);

      return newUrl;
    } catch (error) {
      console.error("ŸÅÿ¥ŸÑ ÿ±ŸÅÿπ ÿßŸÑÿµŸàÿ±ÿ©:", error);
      throw error;
    }
  }

  // ‚úÖ ŸáŸÜÿß ÿßŸÑÿØÿßŸÑÿ© ÿßŸÑŸÑŸä ŸÉÿßŸÜÿ™ ÿ¨ŸàŸá useUpdateAccount ÿ®ŸÇÿ™ ŸÖÿ®ÿßÿ¥ÿ±ÿ©
  async function updateAccountInfo(updatedFields) {
    const token = localStorage.getItem("token");
    if (!token) throw new Error("ŸÑŸÖ Ÿäÿ™ŸÖ ÿßŸÑÿπÿ´Ÿàÿ± ÿπŸÑŸâ ÿßŸÑÿ™ŸàŸÉŸÜ");

    try {
      const response = await fetch(
        "https://projectapi-ekhpcndsdgbahqhm.canadacentral-01.azurewebsites.net/api/Account/update",
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify(updatedFields),
        }
      );

      if (!response.ok) {
        const result = await response.json();
        throw new Error(result.message || "ŸÅÿ¥ŸÑ ÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑÿ≠ÿ≥ÿßÿ®");
      }

      let updatedData;
      try {
        updatedData = await response.json();
      } catch {
        updatedData = updatedFields;
      }

      const oldData = JSON.parse(localStorage.getItem("userData") || "{}");
      const mergedData = { ...oldData, ...updatedData };

      localStorage.setItem("userData", JSON.stringify(mergedData));
      setSaveData(mergedData);

      return mergedData;
    } catch (error) {
      console.error("ŸÅÿ¥ŸÑ ÿßŸÑÿ™ÿ≠ÿØŸäÿ´:", error);
      throw error;
    }
  }

  return (
    <LoginClientContext.Provider
      value={{
        error,
        isLoading,
        getUserData,
        submitForm,
        saveData,
        token,
        updateProfilePicture,
        updateAccountInfo, // ‚úÖ ÿÆŸÑŸäŸáÿß ŸáŸÜÿß ŸÖÿ®ÿßÿ¥ÿ±ÿ©
      }}
    >
      {children}
    </LoginClientContext.Provider>
  );
}
