"use client";

import React, { useState, useEffect } from "react";
import { useLoginClient } from "../context/regester/login_context";

export default function UpdateAccount() {
  const [modalType, setModalType] = useState<null | "profile" | "phone" | "addresses">(null);

  return (
    <div style={{ minHeight: "100vh", background: "#fafafa", padding: "32px 0" }}>
      <div style={{ maxWidth: 1100, margin: "0 auto", display: "flex", gap: 32, alignItems: "flex-start" }}>
        <div style={{ flex: 2, background: "#fff", borderRadius: 12, boxShadow: "0 1px 4px rgba(0,0,0,0.04)", padding: 32 }}>
          <div style={{ color: "#888", fontSize: 14, marginBottom: 8 }}>
            Account &gt; <span style={{ color: "#222" }}>My profile</span>
          </div>
          <h2 style={{ fontSize: 28, fontWeight: 600, marginBottom: 32 }}>My profile</h2>
          <ProfileItem title="Profile Information" desc="Edit your profile picture, name, surname" onClick={() => setModalType("profile")} />
          <ProfileItem title="mobile phone number" desc="modify your mobile phone number" onClick={() => setModalType("phone")} />
          <ProfileItem title="Address Preferences" desc="edit your address preference" last onClick={() => setModalType("addresses")} />
        </div>

        <div style={{ flex: 1, display: "flex", flexDirection: "column", gap: 24 }}>
          <InfoCard title="Why isn‚Äôt my info shown here?" desc="We‚Äôre hiding some account details to protect your identity." icon="üôà" />
          <InfoCard title="Which details can be edited?" desc="Some details can‚Äôt be changed. We may ask for ID verification." icon="üîí" />
          <InfoCard title="What info is shared with others?" desc="Only released after booking confirmation." icon="üîó" />
        </div>
      </div>

      <div style={{ marginTop: 48, textAlign: "center", color: "#888", fontSize: 13 }}>
        2025 Iogo, Inc. ¬∑ Terms of Use ¬∑ Privacy Policy ¬∑ Accessibility ¬∑ Services
      </div>

      {modalType === "profile" && <ProfileModal onClose={() => setModalType(null)} />}
      {modalType === "phone" && <PhoneModal onClose={() => setModalType(null)} />}
      {modalType === "addresses" && <AddressModal onClose={() => setModalType(null)} />}
    </div>
  );
}

function ProfileItem({ title, desc, last, onClick }) {
  return (
    <div
      style={{ borderBottom: last ? "none" : "1px solid #eee", padding: "20px 0", display: "flex", justifyContent: "space-between", alignItems: "center", cursor: onClick ? "pointer" : "default" }}
      onClick={onClick}
    >
      <div>
        <div style={{ fontWeight: 500, fontSize: 17 }}>{title}</div>
        <div style={{ color: "#888", fontSize: 14 }}>{desc}</div>
      </div>
      <span style={{ fontSize: 22, color: "#bbb" }}>&#8250;</span>
    </div>
  );
}

function InfoCard({ title, desc, icon }) {
  return (
    <div style={{ background: "#fff", borderRadius: 12, boxShadow: "0 1px 4px rgba(0,0,0,0.04)", padding: 24, display: "flex", gap: 16, alignItems: "flex-start" }}>
      <div style={{ fontSize: 28, background: "#f2f6f7", borderRadius: "50%", width: 44, height: 44, display: "flex", alignItems: "center", justifyContent: "center" }}>{icon}</div>
      <div>
        <div style={{ fontWeight: 500, marginBottom: 8 }}>{title}</div>
        <div style={{ color: "#888", fontSize: 14 }}>{desc}</div>
      </div>
    </div>
  );
}

function ProfileModal({ onClose }) {
  const { updateAccountInfo } = useLoginClient();
  const [name, setName] = useState("");
  const [companyName, setCompanyName] = useState("");

  useEffect(() => {
    const data = localStorage.getItem("userData");
    if (data) {
      const user = JSON.parse(data);
      setName(user.displayName || "");
      setCompanyName(user.companyName || "");
    }
  }, []);

  const handleSave = async () => {
    try {
      await updateAccountInfo({ displayName: name, companyName });
      onClose();
    } catch (err) {
      alert("ŸÅÿ¥ŸÑ ÿßŸÑÿ™ÿ≠ÿØŸäÿ´: " + err.message);
    }
  };

  return (
    <div style={modalBackdropStyle}>
      <div style={modalContainerStyle}>
        <button onClick={onClose} style={closeBtnStyle}>&#10005;</button>
        <h3 style={{ fontWeight: 600, fontSize: 20 }}>Edit Personal Info</h3>
        <label>Name</label>
        <input value={name} onChange={(e) => setName(e.target.value)} style={inputStyle} />
        <label>Company Name</label>
        <input value={companyName} onChange={(e) => setCompanyName(e.target.value)} style={inputStyle} />
        <button style={saveBtnStyle} onClick={handleSave}>Save</button>
      </div>
    </div>
  );
}

function PhoneModal({ onClose }) {
  const { updateAccountInfo } = useLoginClient();
  const [phoneNumber, setPhoneNumber] = useState("");

  useEffect(() => {
    const data = localStorage.getItem("userData");
    if (data) {
      const user = JSON.parse(data);
      setPhoneNumber(user.phoneNumber || "");
    }
  }, []);

  const handleSave = async () => {
    try {
      await updateAccountInfo({ phoneNumber });
      onClose();
    } catch (err) {
      alert("ŸÅÿ¥ŸÑ ÿßŸÑÿ™ÿ≠ÿØŸäÿ´: " + err.message);
    }
  };

  return (
    <div style={modalBackdropStyle}>
      <div style={modalContainerStyle}>
        <button onClick={onClose} style={closeBtnStyle}>&#10005;</button>
        <h3 style={{ fontWeight: 600, fontSize: 20 }}>Edit Phone Number</h3>
        <label>Phone Number</label>
        <input value={phoneNumber} onChange={(e) => setPhoneNumber(e.target.value)} style={inputStyle} />
        <button style={saveBtnStyle} onClick={handleSave}>Save</button>
      </div>
    </div>
  );
}

function AddressModal({ onClose }) {
  const { updateAccountInfo } = useLoginClient();
  const [address, setAddress] = useState("");
  const [cityName, setCityName] = useState("");
  const [buildingNumber, setBuildingNumber] = useState("");

  useEffect(() => {
    const data = localStorage.getItem("userData");
    if (data) {
      const user = JSON.parse(data);
      setAddress(user.address || "");
      setCityName(user.cityName || "");
      setBuildingNumber(user.buildingNumber || "");
    }
  }, []);

  const handleSave = async () => {
    try {
      await updateAccountInfo({ address, cityName, buildingNumber });
      onClose();
    } catch (err) {
      alert("ŸÅÿ¥ŸÑ ÿßŸÑÿ™ÿ≠ÿØŸäÿ´: " + err.message);
    }
  };

  return (
    <div style={modalBackdropStyle}>
      <div style={modalContainerStyle}>
        <button onClick={onClose} style={closeBtnStyle}>&#10005;</button>
        <h3 style={{ fontWeight: 600, fontSize: 20 }}>Edit Address Information</h3>
        <label>Address</label>
        <input value={address} onChange={(e) => setAddress(e.target.value)} style={inputStyle} />
        <label>City</label>
        <input value={cityName} onChange={(e) => setCityName(e.target.value)} style={inputStyle} />
        <label>Building Number</label>
        <input value={buildingNumber} onChange={(e) => setBuildingNumber(e.target.value)} style={inputStyle} />
        <button style={saveBtnStyle} onClick={handleSave}>Save</button>
      </div>
    </div>
  );
}

const inputStyle = {
  width: "100%",
  borderRadius: 6,
  border: "1px solid #eee",
  fontSize: 16,
  padding: "10px 12px",
  outline: "none",
  fontWeight: 500,
  boxSizing: "border-box",
};

const saveBtnStyle = {
  padding: "10px 24px",
  borderRadius: 6,
  border: "none",
  background: "#22c55e",
  color: "#fff",
  fontWeight: 500,
  cursor: "pointer",
  marginTop: 12,
  alignSelf: "flex-end",
};

const modalBackdropStyle = {
  position: "fixed",
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  background: "rgba(0,0,0,0.15)",
  zIndex: 1000,
  display: "flex",
  justifyContent: "flex-end",
};

const modalContainerStyle = {
  background: "#fff",
  width: 400,
  height: "100%",
  borderRadius: "16px 0 0 16px",
  boxShadow: "0 4px 24px rgba(0,0,0,0.10)",
  padding: 32,
  display: "flex",
  flexDirection: "column",
  gap: 16,
};

const closeBtnStyle = {
  position: "absolute",
  top: 16,
  right: 16,
  background: "none",
  border: "none",
  fontSize: 22,
  cursor: "pointer",
  color: "#888",
};
